/* Page Styles */
body {
  padding: 0;
  margin: 0;
  width: 100vh;
  height: 300vh;
  background-color: black;
  /* background-image: url("./images/BG.png");
  background-size: cover; */
  color: white;
  overflow-x: hidden;
}
a {
  text-decoration: none;
}
a.name:link,
a.name:visited {
  color: #e51b88;
}
a.name:hover {
  color: white;
  font-size: 1.1em;
}
a.nav:link,
a.nav:visited {
  color: white;
}
a.nav:hover {
  color: #e51b88;
  font-size: 1.4em;
}
.outline-font {
  font-family: "Londrina Outline", cursive;
}
.thin-font {
  font-family: "Londrina Solid", cursive;
}
.bg {
  width: 100vw;
  z-index: index -999;
  position: absolute;
}
/* Nav Bar Styles */
.header {
  width: 90vw;
  height: auto;
  padding: 5px;
  padding-left: 5vw;
  padding-right: 5vw;
  position: fixed;
  font-size: x-large;
  /* background-color: black; */
  display: inline-grid;
  grid-template-columns: 1fr 2fr;
  justify-content: space-between;
  z-index: 99;
}
.navigation {
  display: inline-grid;
  grid-template-columns: repeat(4, 1fr);
  /* display:flex; */
  width: 50vw;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  justify-self: end;
  color: white;
}

/* Landing/Intro Component Styles */
.landing {
  width: 100vw;
  /* max-width: 100%; */
  height: 100vh;
  margin: 0;
  /* padding-left: 10vw;
  padding-right: 10vw; */
  /* position: relative; */
  /* background-color: black; */
}
.TopLeft {
  width: 80vw;
  height: auto;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.BottomRight {
  width: 80vw;
  height: auto;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.greeting {
  width: 45vw;
  max-width: 100%;
  font-size: 120px;
  font-size: 10vw;
  line-height: 50px;
  line-height: 8vw;
  color: white;
  position: absolute;
  /* padding-left: 1vw;
  padding-bottom: 1vw; */
  top: 60%;
  left: 50%;
  transform: translate(0%, -120%);
  overflow: visible;
}
.codeNav {
  width: 45vw;
  height: 20vw;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-100%, 0%);
  font-size: 5vw;
}
.name {
  color: #e51b88;
}
/* About section styles */
.about {
  z-index: 1;
  width: 100vw;
  height: 750px;
}
